import React, { useState, useEffect, useCallback, useRef } from 'react';
import { useSpring, animated } from 'react-spring';
import ResizeObserver from 'resize-observer-polyfill';

import ContactIcon from '../PulsingIcons/ContactIcon';
import './Faq.css';

const Faq = () => {

    const questionsAnswers = [
        {
            id: 0,
            question: 'Куда я попал?',
            answerHeight: 29,
            answer: <p>Юкарихон: стрелы и строфы» — это городская школа японистики в Москве, посвящённая Средневековой Японии XII-XVI веков.</p>,
        },
        {
            id: 1,
            question: 'Что будет происходить?',
            answerHeight: 98,
            answer: <div>
                <p>27 октября — открытие Школы, выступление реконструкторов, открытая дискуссия «Японист в пост-миллениум».<br />2-3 ноября — вака-баттл, кинопоказ, встреча разговорного клуба, лекции, интервью, беседы, мастер-классы, закрытие Школы.</p>
                <p>Суммарно будет проведено 30 мероприятий, из них 9 — беседы, открытые встречи деятельных японистов с аудиторией, в ходе которых ведущие расскажут о собственном пути к изучению Японии — лайфхаках и особенностях познания в той или иной области.</p>
            </div>,
        },
        {
            id: 2,
            question: 'Зачем это мне?',
            answerHeight: 112,
            answer: <ul>
                <li>получить знания о Японии, ранее доступные только в ВУЗах, сломить стереотипы и углубить понимание;</li>
                <li>увидеть японистику по-разному — познакомиться с блоггерами, игротехниками, писателями и мастерами искусств;</li>
                <li>понять наконец, что можно делать с образованием япониста;</li>
                <li>найти единомышленников с горящими глазами и вписаться в японоведческий проект;</li>
                <li>попробовать себя в различных областях прикладной японистики.</li>
            </ul>,
        },
        {
            id: 3,
            question: 'Как участвовать?',
            answerHeight: 96,
            answer: <p>Во-первых, бесплатно и без возрастных ограничений.<br />Во-вторых, в библиотеке «Научка» по адресу ул. Дубининская, д. 20 (5 минут от метро «Павелецкая») или дистанционно.<br />В-третьих, места для очного участия ограничены, поэтому записывайтесь заранее.<br />Для активных участников приготовлены призы.</p>,
        },
        {
            id: 4,
            question: 'Кто организует?',
            answerHeight: 51,
            answer: <p>Команда «Юкарихон» — 20 молодых и амбициозных японистов, дизайнеров и маркетологов, выступающие в качестве посредников между различными сообществами российской японистики.</p>,
        },
    ];

    const [questionOpened, setQuestionOpened] = useState(-1);

    const refs = [useRef(), useRef(), useRef(), useRef(), useRef()]
    const [height0, setHeight0] = useState(0)
    const [height1, setHeight1] = useState(0)
    const [height2, setHeight2] = useState(0)
    const [height3, setHeight3] = useState(0)
    const [height4, setHeight4] = useState(0)

    useEffect(() => {
        const ro = [
            new ResizeObserver(([entry]) => {
                setHeight0(entry.target.scrollHeight);
            }),
            new ResizeObserver(([entry]) => {
                setHeight1(entry.target.scrollHeight);
            }),
            new ResizeObserver(([entry]) => {
                setHeight2(entry.target.scrollHeight);
            }),
            new ResizeObserver(([entry]) => {
                setHeight3(entry.target.scrollHeight);
            }),
            new ResizeObserver(([entry]) => {
                setHeight4(entry.target.scrollHeight);
            }),
        ];

        refs.forEach((ref, i) => {
            if (ref.current) {
                ro[i].observe(ref.current)
            }
        })
    
        return () => {
            ro.forEach(r => r.disconnect());
        }
    }, []);

    const propsArray = [
        useSpring({
            from: { opacity: 0, height: 0 },
            to: { opacity: (questionOpened === 0) ? 1 : 0, height: (questionOpened === 0) ? height0 : 0 },
        }),
        useSpring({
            from: { opacity: 0, height: 0 },
            to: { opacity: (questionOpened === 1) ? 1 : 0, height: (questionOpened === 1) ? height1 : 0 },
        }),
        useSpring({
            from: { opacity: 0, height: 0 },
            to: { opacity: (questionOpened === 2) ? 1 : 0, height: (questionOpened === 2) ? height2 : 0 },
        }),
        useSpring({
            from: { opacity: 0, height: 0 },
            to: { opacity: (questionOpened === 3) ? 1 : 0, height: (questionOpened === 3) ? height3 : 0 },
        }),
        useSpring({
            from: { opacity: 0, height: 0 },
            to: { opacity: (questionOpened === 4) ? 1 : 0, height: (questionOpened === 4) ? height4 : 0 },
        })
    ];

    const getQuestionStyle = (questionNum) => (questionNum === questionOpened) ? 'question-opened question' : 'pointer question';

    const createQuestionAnswer = (questionNum) => {
            return <div>
                <p onClick={() => setQuestionOpened(questionNum)} className={getQuestionStyle(questionNum)}>{questionsAnswers[questionNum].question}</p>
                <animated.div className="answer" style={propsArray[questionNum]}>
                    <div ref={refs[questionNum]}>{questionsAnswers[questionNum].answer}</div>
                </animated.div>
            </div>
    };

    return (
        <div className="padded-container faq-container">
            <h1>Часто задаваемые вопросы</h1>
            {createQuestionAnswer(0)}
            <hr />
            {createQuestionAnswer(1)}
            <hr />
            {createQuestionAnswer(2)}
            <hr />
            {createQuestionAnswer(3)}
            <hr />
            {createQuestionAnswer(4)}
            <hr />
            <ContactIcon />
            {/* <p style={{position: 'fixed', botoom: 10, right: 70}}>Ask question -></p> */}
            {/* <p className="contact-us">Не нашли ответа? Пишите нам.</p> */}
        </div>
    );
}

export default Faq;