import React from 'react';

import './Backdrop.css';

const Backdrop = ({ show, click }) => {
    let backdropClasses = 'backdrop sticky t1'
    if (show) {
        backdropClasses = 'backdrop sticky t2'
    }

    return (
        <div className={backdropClasses} onClick={click} />
    );
}

export default Backdrop;

