import React from 'react';

import Card from './Card.js';
import './Events.css';

const Events = () => {
    
    return (
        <div className="events-container">
            <div className="events-header">
                <h2>Первый день</h2>
                <p>27 Октября 2019<br />Суббота</p>
            </div>
            <div className="groups-cnt">
                <p className="time" style={{visibility: 'hidden'}}>10:00</p>
                <div className="ev-cards">
                <p className="violet">1 Поток</p>
                <p className="blue">2 Поток</p>
                </div>
            </div>
            <div className="ev">
                <p className="time">10:00</p>
                <div className="ev-cards">
                    <div className="card ev-violet">
                        <p>Лекция / интерактивная дискуссия по ведущим святилищам синто: в Японии в Средние века</p>
                        <p>Пупкин В.П.</p>
                    </div>
                    <div className="card ev-blue">
                        <p>Лекция о системе международных отношений на Дальнем Востоке в Средние века</p>
                        <p>Пупкин В.П. 2</p>
                    </div>
                </div>
            </div>
            <hr />
            {/* <Card /> */}
        </div>
    );
}

export default Events;