import React from 'react';
import { Link } from 'react-router-dom';

import logoNav from '../../assets/images/logo-nav.svg';
import toggleMenu from '../../assets/images/toggle-menu.svg';
import exit from '../../assets/images/exit.svg';
import DrawerToggleButton from '../SideDrawer/DrawerToggleButton';
import './Nav.css';

const Nav = ({ drawerToggleClickHandler }) => {
    return (
        <header>
            <Link to="/"><img src={logoNav} alt="Logo" className="nav-logo" /></Link>
            <nav>
                <div className="toggle-btn" onClick={drawerToggleClickHandler}>
                    {/* <img src={toggleMenu} alt="Открыть меню" /> */}
                    <DrawerToggleButton />
                </div>
                <ul className="nav-list">
                    <Link to="/about"><li>о школе</li></Link>
                    <Link to="/events"><li>мероприятия</li></Link>
                    <Link to="/lecturers"><li>ведущие</li></Link>
                    <Link to="/contacts"><li>контакты</li></Link>
                    <Link to="/faq"><li>FAQ</li></Link>
                </ul>
            </nav>
        </header>
    );
}

export default Nav;