import React from 'react';

import SignIcon from '../PulsingIcons/SignIcon';
import './Home.css';

const Home = () => {
    return (
        <div className="container">
            <img src={require('../../assets/images/logo-home.svg')} alt="Лого" className="logo-home" />
            <h1 className="head">Юкарихон: стрелы и строфы</h1>
            <p className="subhead">городская школа японистики в Москве</p>
            <p className="subhead">27 октября — 3 ноября</p>
            <SignIcon />
        </div>
    );
}

export default Home;