import React, { useState } from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

import Nav from './components/Nav/Nav';
import Home from './components/Home/Home';
import About from './components/About/About';
import Events from './components/Events/Events';
import Lecturers from './components/Lecturers/Lecturers';
import Contacts from './components/Contacts/Contacts';
import Faq from './components/Faq/Faq';
import Backdrop from './components/Backdrop/Backdrop';
import SideDrawer from './components/SideDrawer/SideDrawer';
import Sign from './components/Sign/Sign';
// import SignIcon from './components/PulsingIcons/SignIcon';
// import ContactIcon from './components/PulsingIcons/ContactIcon';
import './App.css';
// import MovieList from './components/context_state/MovieList';
