const App = () => {

  const [sideDrawerOpen, setSideDrawerOpen] = useState(false);

  const drawerToggleClickHandler = () => {setSideDrawerOpen(!sideDrawerOpen)};
  const closeToggleClickHandler = () => {setSideDrawerOpen(false)};

  // let backdrop;
  // if (sideDrawerOpen) {
  //   backdrop = <Backdrop click={backdropClickHandler} />;
  // }

  return (
    <Router>
      <Nav drawerToggleClickHandler={drawerToggleClickHandler} />
      <SideDrawer closeButtonClick={closeToggleClickHandler} show={sideDrawerOpen}/>
      <Backdrop show={sideDrawerOpen} click={closeToggleClickHandler} />
      <Switch>
        <Route path="/" exact component={Home} />
        <Route path="/about" component={About} />
        <Route path="/events" component={Events} />
        <Route path="/lecturers" component={Lecturers} />
        <Route path="/contacts" component={Contacts} />
        <Route path="/faq" component={Faq} />
      </Switch>
      {/* <Sign /> */}
      {/* <SignIcon /> */}
      {/* <ContactIcon /> */}
    </Router>
    // <MovieProvider>
    //   <div className="App">
    //     <Nav />
    //     <MovieList />
    //   </div>
    // </MovieProvider>
  );
}

export default App;
