import React from 'react';
import { Link } from 'react-router-dom';

import exit from '../../assets/images/exit.svg';
import './SideDrawer.css';

const SideDrawer = ({ closeButtonClick, show }) => {
    let drawerClasses = 'side-drawer sticky'
    if (show) {
        drawerClasses = 'side-drawer sticky open'
    }
    return (
        <div className={drawerClasses}>
            <ul>
                <li className="exit-btn"><img src={exit} alt="Выйти из меню" onClick={closeButtonClick} /></li>
                <Link to="/about"><li>о школе</li></Link>
                <Link to="/events"><li>мероприятия</li></Link>
                <Link to="/lecturers"><li>ведущие</li></Link>
                <Link to="/contacts"><li>контакты</li></Link>
                <Link to="/faq"><li>FAQ</li></Link>
            </ul>
        </div>
    );
};

export default SideDrawer;