import React from 'react';

import './DrawerToggleButton.css';

const DrawerToggleButton = ({ hovered }) => {
    let toggleColorClass;
    if (hovered) {
        toggleColorClass = 'toggle-button-line toggle-violet';
    } else {
        toggleColorClass = 'toggle-button-line toggle-white';
    }
    return (
        <div className="toggle-button">
            <div className='toggle-button-line v-line' />
            <div className='toggle-button-line vb-line' />
            <div className='toggle-button-line b-line' />
        </div>
    );
}

export default DrawerToggleButton;