import React from 'react';

import logoAbout from '../../assets/images/logo-about.svg';
import './About.css';

const About = () => {
    return (
        <div className="container">
            <img src={logoAbout} alt="Лого" />
            <p>Самураи не служат Императору, Фудзиямы не существует, а сакура не цветёт круглый год.</p>
            <p>Впервые профессиональные японисты выйдут на открытую дискуссию с любителями.
                 Впервые успешные исследователи поделятся лайфхаками с участниками.</p>
            <p>Российская японистика заявляет: мы готовы рассказать о вопросах сложных, но чертовски интересных.</p>
            <p>30 мероприятий о Средневековой Японии! 
                Вака-баттл, кинопоказ, интервью, выступление реконструкторов, публичная дискуссия и другие.</p>
            <p>Бесплатно для всех участников.</p>
            {/* <div className="test">
                Test
            </div> */}
        </div>
    );
}

export default About;